<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10 zd-cm-res-fl"
              custom-icon="zd-common-icon-add" @click="toAddOrder">
        出库单
      </Button>
      <div class="zd-common-fr zd-cm-res-fr">
        <DatePicker :editable="false" type="daterange" :options="dateOptions" placeholder="日期"
                    class="zd-common-input-h38 zd-common-fl zd-common-mr10 zd-cm-w220" @on-change="changeDate"></DatePicker>
        <Select clearable v-model="type" placeholder="全部出库类型" class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10">
          <Option v-for="item in typeArr" :value="item.code" :key="item.code">{{ item.name }}</Option>
        </Select>
        <Select clearable v-model="warehouse" placeholder="全部出库仓库" class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10">
          <Option v-for="item in warehouseArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <Input v-model="orderNO" :maxlength="50" placeholder="出库单号" class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10"/>
        <Select
          ref="leader"
          class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10"
          placeholder="领用人"
          v-model="leader"
          filterable
          remote
          :remote-method="remoteMethod"
          :loading="loading">
          <Option v-for="option in leaderArr" :value="option.id" :key="option.id">{{option.name}}</Option>
        </Select>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>
    </div>

    <!--查看-->
    <Modal v-model="viewModal" :mask-closable="false" :width="1100" class="print">
      <h3 slot="header" class="zd-common-modal-header">
        <span>查看出库单</span>
      </h3>
      <div class="zd-common-clearFix">
        <span class="zd-common-fl zd-common-mr14">出库单号：<span class="zd-common-font-weight">{{ allDetail.num }}</span></span>
        <span class="zd-common-fl zd-common-mr14">出库日期：<span class="zd-common-font-weight">{{ allDetail.date }}</span> </span>
        <span class="zd-common-fl zd-common-mr14">出库仓库：<span class="zd-common-font-weight">{{ allDetail.warehouseName }}</span> </span>
        <br/>
        <span class="zd-common-fl zd-common-mr14 zd-common-mt10">出库类型：<span class="zd-common-font-weight">{{ allDetail.kindCode }}</span> </span>
        <span class="zd-common-fl zd-common-mr14 zd-common-mt10">部门：<span class="zd-common-font-weight">{{ allDetail.departmentName }}</span></span>
        <span class="zd-common-fl zd-common-mr14 zd-common-mt10">领用人：<span class="zd-common-font-weight">{{ allDetail.buyserName }}</span></span>
      </div>

      <div class="zd-common-h300 zd-common-mt10">
        <vue-scroll>
          <Table border :columns="goodsCols" :data="goodsData" :summary-method="handleSummary" show-summary></Table>
        </vue-scroll>
      </div>

      <div class="zd-common-mt10 zd-common-clearFix">
        <span class="zd-common-fl">制单人：{{ allDetail.userName }}</span><span class="zd-common-fl zd-common-ml20">制单日期：{{ allDetail.productDate }}</span>
      </div>
      <div class="zd-common-mt10 zd-common-clearFix">
        <span class="zd-common-clearFix"><span class="zd-common-fl">备注：</span><span class="zd-common-fl zd-cm-remark">{{ allDetail.note }}</span></span>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="printHandle">打印</Button>
        <Button @click="viewModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>

    <!--删除-->
    <Modal v-model="delModal" :mask-closable="false" :width="590">
      <h3 slot="header" class="zd-common-modal-header">
        <span>删除出库单</span>
      </h3>
      <Alert type="warning" show-icon class="zd-common-warning-c">删除出库单将同步删除该出库单中所有已出库物品，导致数据统计错误，请谨慎操作！</Alert>
      <Form ref="deleteForm" :model="deleteForm">
        <FormItem label="删除原因：" class="zd-common-mb0">
          <Input class="zd-common-fl" v-model.trim="deleteForm.reason" placeholder="" :maxlength="100" type="textarea" :autosize="{minRows: 3,maxRows: 3}"></Input>
        </FormItem>
      </Form>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="handleSubmit" :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="delModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>

    <!--签字-->
    <Modal v-model="signModal" :mask-closable="false" :width="590">
      <h3 slot="header" class="zd-common-modal-header">
        <span>签字-{{ signOrderNO }}</span>
      </h3>
      <Form ref="signForm" :model="signForm" :rules="ruleValidate" :label-width="0" class="zd-common-tc">
        <FormItem label="" prop="uploadId">
          <div class="demo-upload-list zd-common-w150" v-for="item in uploadList">
            <template v-if="item.status === 'finished'">
              <img :src="item.url">
              <div class="demo-upload-list-cover">
                <Icon type="ios-eye-outline" @click.native="handleView(item.url)"></Icon>
                <Icon type="ios-trash-outline" @click.native="handleRemove"></Icon>
              </div>
            </template>
            <template v-else>
              <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
            </template>
          </div>
          <!--:before-upload="handleBeforeUpload"-->
          <Upload
            name="filedata"
            ref="upload"
            :show-upload-list="false"
            :format="['jpg','jpeg','png']"
            type="drag"
            :action="serverPath+'/component/attachment!saveUploadFile2.action'"
            :max-size="2048"
            :on-success="handleSuccess"
            :on-format-error="handleFormatError"
            :on-exceeded-size="handleMaxSize"
            class="zd-common-w150 zd-common-auto"
          >
              <Button type="primary" class="zd-common-w150">上传签字图片</Button>
          </Upload>
          <p class="zd-common-fs12 zd-common-des-font-c">允许上传png、jpg、jpeg格式图片</p>
          <input type="text" v-model="signForm.uploadId" hidden/>
          <Modal v-model="visible" :width="60">
            <h3 slot="header" class="zd-common-modal-header">
              <span>预览签字</span>
            </h3>
            <vue-scroll>
              <div class="zd-common-font-size-base zd-common-lh30 zd-common-tc zd-common-h500">
                <img :src="uploadPicUrl" v-if="visible" style="max-width: 100%">
              </div>
            </vue-scroll>
            <div slot="footer" class="zd-common-modal-footer zd-common-pt0"></div>
          </Modal>
        </FormItem>
      </Form>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt0">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="signSubmit('signForm')" :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="signModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>

  </div>
</template>
<script>
  import { utils } from '@comm/script/utils/index'
  export default {
    data() {
      return {
        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '出库单号',
            align: 'center',
            key: 'putInOrder'
          },
          {
            title: '出库日期',
            align: 'center',
            key: 'putInDate',
          },
          {
            title: '出库类型',
            align: 'center',
            key: 'putInType',
          },
          {
            title: '出库仓库',
            align: 'center',
            key: 'warehouse',
          },
          {
            title: '领用人',
            align: 'center',
            key: 'user',
          },
          {
            title: '物品数量',
            align: 'center',
            key: 'number'
          },
          {
            title: '制单人',
            align: 'center',
            key: 'orderPerson'
          },
          {
            title: '制单日期',
            align: 'center',
            key: 'orderDate'
          },
          {
            title: '操作',
            align: 'center',
            key: 'action',
            width:300,
            render: (h, params) => {
              if(params.row.signFlag){
                return h('div', [
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px',
                      marginRight:'10px'
                    },
                    on: {
                      click: () => {
                        this.showViewModal(params.row.id);
                      }
                    }
                  }, '查看'),
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px',
                      marginRight:'10px'
                    },
                    on: {
                      click: () => {
                        this.showSignModal(params.index);
                      }
                    }
                  }, '签字'),
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px',
                      marginRight:'10px'
                    },
                    on: {
                      click: () => {
                        this.showModifyModal(params.row.id);
                      }
                    }
                  }, '修改'),
                  h('Button', {
                    props:{
                      type:'error',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px',
                    },
                    on: {
                      click: () => {
                        this.showDelModal(params.row.id);
                      }
                    }
                  }, '删除'),
                ]);
              }else{
                return h('div', [
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px'
                    },
                    on: {
                      click: () => {
                        this.showViewModal(params.row.id);
                      }
                    }
                  }, '查看')
                ]);
              }

            }
          }
        ],
        data: [],
        pageSize:10,
        pageNum:1,
        total:0,
        //出库单查看
        saveLoading:false,
        goodsCols:[
          {
            type: 'index',
            title:'序号',
            align: 'center',
            width:65
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code',
          },
          {
            title: '物品分类',
            align: 'center',
            key: 'classify',
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'model',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',
            width:65
          },
          {
            title: '出库数量',
            align: 'center',
            key: 'number',
          },
          {
            title: '金额',
            align: 'center',
            key: 'amount',
          }
        ],
        goodsData:[
          {
            name:'电池',
            code:'xm5hdc',
            classify:'办公',
            brand:'南孚',
            model:'p34',
            unit:'个',
            number:3,
            amount:360,
          }
        ],
        viewModal:false,
        allDetail:{},
        //删除
        delModal:false,
        deleteForm:{
          reason:''
        },
        delId:'',
        //查询
        dateOptions: this.dateRange,
        startDate:'',
        endDate:'',
        typeArr:[],
        type:'',
        warehouseArr:[],
        warehouse:'',
        orderNO:'',
        leader:'',
        leaderArr:[],
        loading:false,
        selectLoading:false,
        //签字
        signId:'',
        signModal:false,
        signOrderNO:'',
        visible: false,
        uploadPicUrl:'',
        signForm:{
          uploadId:""
        },
        ruleValidate: {
          uploadId: [
            {required: true, message: '请选择文件', trigger: 'blur'}
          ]
        },
        uploadList: [],
      }
    },
    created(){
      this.getOutWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.orderNO,this.leader);
      this.getOutKindList().then((response)=>{
        this.typeArr = response.data;
      });
      this.getChargedStoreRoom().then((response)=>{
        this.warehouseArr = response.data;
      });
    },
    methods: {
      //打印
      printHandle(){
        // window.print();
        this.$print(this.$refs.print)
      },
      //选择领用人
      remoteMethod(name){
        this.selectLeader(name).then((res)=>{
          this.leaderArr = res.data;
        })
      },
      //日期
      changeDate(arr){
        this.startDate = arr[0];
        this.endDate = arr[1];
      },
      //添加出库单
      toAddOrder(){
        this.$router.push({path:'/addOutOrder'});
      },
      //查看
      showViewModal(id){
        this.viewModal = true;
        this.getOutWarehouseRecordInfo(id);
      },
      getOutWarehouseRecordInfo(id) {
        this.goodsData = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getOutWarehouseRecordInfo.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            let aData = response.data;
            this.allDetail = aData;
            aData.goodsList.map((item)=>{
              this.goodsData.push(
                {
                  name:item.goodsInfoName,
                  code:item.code,
                  classify:item.goodsKindName,
                  brand:item.brand,
                  model:item.size,
                  unit:item.unit,
                  number:item.goodsNum,
                  amount:item.totalPrice
                }
              )
            })

          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //修改
      showModifyModal(id){
        this.$router.push({ path: `/editOutOrder/${id}` });
      },
      //合计
      handleSummary ({ columns, data }) {
        const sums = {};
        columns.forEach((column, index) => {
          const key = column.key;
          if (index === 0) {
            sums[key] = {
              key,
              value: '合计'
            };
            return;
          }
          const values = data.map(item => Number(item[key]));
          if (!values.every(value => isNaN(value)) && (index===7 || index===8)) {
            const v = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[key] = {
              key,
              value: v
            };
          } else {
            sums[key] = {
              key,
              value: ''
            };
          }
        });
        return sums;
      },
      //删除
      showDelModal(id){
        this.delModal = true;
        this.delId = id;
        this.deleteForm.reason = '';
      },
      deleteOutWarehouseRecord(id,deleteCause) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!deleteOutWarehouseRecord.action',
          data:{
            id:id,
            deleteCause:deleteCause
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData!==""){
              this.postWarningTip(aData);
            }
            this.saveLoading = false;
            this.getOutWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.orderNO,this.leader);
          })
          .catch((error) => {
            this.saveLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      handleSubmit() {
        this.saveLoading=true;
        this.delModal = false;
        this.deleteOutWarehouseRecord(this.delId,this.deleteForm.reason);
      },
      //上传签字
      handleSuccess (res, file){
        this.signForm.uploadId = res.id;
        file.url = this.serverPath+"/sr/storeroomData!showTempPic.action?attachmentId="+res.id;
        this.uploadList = [file];
        this.$refs['signForm'].validate();
      },
      handleFormatError (file) {
        this.postWarningTip('图片格式错误');
      },
      handleMaxSize (file) {
        this.postWarningTip('最多上传2M大小的图片');
      },

      showSignModal(index){
        this.signForm.uploadId = '';
        this.signModal = true;
        this.signOrderNO = this.data[index].putInOrder;
        this.signId = this.data[index].id;
        this.uploadList = [];
      },
      handleView (pic) {
        this.uploadPicUrl = pic;
        this.visible = true;
      },
      handleRemove() {
        this.uploadList = [];
        this.signForm.uploadId = '';
      },
      signSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.signModal = false;
            this.saveLoading=true;
            this.signOutWarehouseRecord(this.signId,this.signForm.uploadId);
          }
        })
      },
      signOutWarehouseRecord(id,attIds) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!signOutWarehouseRecord.action',
          data:{
            id:id,
            attIds:attIds
          }
        })
          .then((response) => {
            this.saveLoading = false;
            this.getOutWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.orderNO,this.leader);
          })
          .catch((error) => {
            this.saveLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },

      //列表
      getOutWarehouseRecordPage(pageNum,pageSize,startDate,endDate,kindCode,storeroomId,num,userId) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getOutWarehouseRecordPage.action',
          data:{
            pageNum:pageNum,
            pageSize:pageSize,
            startDate:startDate,
            endDate:endDate,
            kindCode:kindCode,
            storeroomId:storeroomId,
            num:num,
            userId:userId
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.list.length>0){
              aData.list.map((item)=>{
                this.data.push(
                  {
                    id:item.id,
                    putInOrder:item.num,
                    putInDate:item.date,
                    putInType:item.kind,
                    warehouse:item.warehouseName,
                    user:item.buyserName,
                    number:item.goodsNum,
                    orderPerson:item.user,
                    orderDate:item.productDate,
                    signFlag:item.signFlag
                  }
                )
              })
            }
            /*if(this.pageNum>aData.totalPageCount){
              this.pageNum = aData.totalPageCount;
              this.getIntoWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.supplier,this.orderNO);
            }*/
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //分页
      changePage(num) {
        this.pageNum = num;
        this.getOutWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.orderNO,this.leader);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.getOutWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.orderNO,this.leader);
      },
      //查询
      selectHandle(){
        this.selectLoading = true;
        this.getOutWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.orderNO,this.leader);
      },
    },
    mounted(){
      this.uploadList = this.$refs.upload.fileList;
    }
  }
</script>
<style lang="less" scoped>
  .ivu-table .demo-table-info-row td{
    background-color: rgba(28, 144, 243, 0.1);
  }
  /*适配*/
  @media screen and (max-width: 1434px){
    .zd-cm-res-fr{
      float: left !important;
    }
    .zd-cm-res-fr > *,.zd-cm-res-fl{
      margin-bottom:10px;
    }
  }
</style>



